body {
  margin: 0;
  padding: 0;

  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  overflow-x: hidden;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

button:focus {
  outline: 0;
}

button:focus,
input[type="button"]:focus {
  outline: none;
}

.home {
  overflow: hidden;
}

.section1 {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-image: url(/static/media/Section1BGDesktop.149819fa.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.section2 {
  width: 100%;
  height: 80vh;
  background-color: #f7f7f7;
  overflow: hidden;
}

.featured-header {
  margin-top: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.featured-header p {
  padding: 0px 12px;
  font-size: 32px;
  font-weight: bold;
  color: #002926;
  font-family: serif;
}

.line {
  width: 35vw;
  height: 1px;
  background-color: #002926;
  opacity: 0.5;
}

.section3 {
  width: 100%;
  background-color: white;
}

@media (max-width: 375px) and (max-height: 667px) {
  .section2 {
    height: 85vh;
  }

  .section3 {
    height: 80vh;
  }
}

@media (max-width: 414px) {
  .featured-header p {
    font-size: 22px;
  }
  .featured-header {
    margin-bottom: 20px;
  }
  .line {
    width: 20vw;
  }
  .navbarDivider {
    height: 8vh;
  }
}

@media (max-width: 975px) and (min-width: 415px) {
  .navbarDivider {
    height: 8vh;
  }
  .featured-header p {
    font-size: 25px;
  }
  .line {
    width: 25vw;
  }
}

.navbar-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 20vh;
  width: 100vw;
  position: absolute;
}

.navbar {
  margin: 0px;
  margin-top: 20px;
}

.container {
  position: absolute;
}

.navbar-button {
  padding: 0;
  color: black;
  text-decoration: none;
  font-size: 32px;
  font-weight: lighter;
  font-family: serif;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

.navbar-button:hover:not(#selected) {
