  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  cursor: pointer;
}

#logo {
  padding: 10px;
  border: 4px inset black;
}

.navbar,
.navbar-item {
  display: inline;
}

.navbar-item {
  padding: 40px;
}

.navbar-item > a {
  color: black;
  text-decoration: none;
  font-size: 32px;
  font-weight: lighter;
  font-family: serif;
}

.navbar-item > a:hover:not(#selected) {
  color: #629894;
  font-size: 34px;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
}

#selected {
  font-weight: bold;
  text-decoration: underline;
}

.mobile-navbar-container {
  display: none;
}

/* Secondary navbar style */
.bar-container {
  width: 100vw;
  position: fixed;
  background-color: white;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.05);
  z-index: 3;
}

.bar-navbar-container {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.bar-navbar-container img {
  width: 8vh;
}

.navbar,
.bar-navbar-item {
  display: inline;
}

.bar-navbar-item {
  padding: 10px;
}

.bar-navbar-button {
  padding: 0;
  color: #629894;
  text-decoration: none;
  font-size: 32px;
  font-weight: lighter;
  font-family: serif;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

.bar-navbar-button:hover:not(#selected) {
  color: #a7a7a7;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  cursor: pointer;
}

#bar-selected {
  color: #002926;
  font-weight: bold;
}

@media (max-width: 1024px) {
  .navbar {
    display: none;
  }

  .bar-container {
    display: none;
  }

  .container {
    top: 0;
    position: fixed;
    z-index: 2;
  }

  .navbar-container {
    display: none;
  }

  .mobile-navbar-container {
    display: -webkit-flex;
    display: flex;
    width: 100vw;
    -webkit-flex-flow: column;
            flex-flow: column;
  }

  .mobile-navbar-header {
    background-color: #f7f7f7;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-right: 15px;
    -webkit-align-items: center;
            align-items: center;
    height: 8vh;
  }

  #nav-button-icon {
    padding: 15px;
    border: none;
    background-color: rgba(0, 0, 0, 0);
  }

  #mobile-logo {
    max-height: 5.5vh;
  }

  .mobile-navbar-drawer {
    display: -webkit-flex;
    display: flex;
    margin: 0;
    padding-bottom: 1px;
    margin-top: -1px;
    height: 92vh;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(247, 247, 247, 0.98);
    max-width: 0vw;
    overflow: hidden;
    box-shadow: 2px 10px 15px rgba(0, 0, 0, 0.1);
    -webkit-transition: max-width 0.5s ease-out;
    transition: max-width 0.5s ease-out;
  }

  .hide {
    display: none;
  }

  .mobile-navbar,
  .navbar-item-drawer {
    display: block;
  }

  .mobile-navbar {
    padding: 10px 10px 10px 20px;
    -webkit-animation: fade 1.3s linear;
            animation: fade 1.3s linear;
    display: none;
  }

  .fade-out {
    -webkit-animation: fade-out 1.3s linear;
            animation: fade-out 1.3s linear;
  }

  .navbar-item-drawer > a {
    text-decoration: none;
    color: black;
    font-size: 23px;
    font-weight: lighter;
  }

  .navbar-item-drawer {
    padding: 20px;
    text-align: center;
  }

  @-webkit-keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-webkit-keyframes fade-out {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  @keyframes fade-out {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  #selected {
    font-weight: bold;
  }
}

